.vendi {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    background-color: rgba(255,220,94,255);
    flex-direction: column;
}

.vendi-image {
    width: 100%;
}

.vendi-title1 {
    color: rgb(41, 41, 129);
    margin-top: 20px;
}

.vendi-title2 {
    color: red;
    margin-top: 10px;
}

.vendi-inputs-container {
    width: 100%;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    margin-top: 20px;
}

.vendi-input-container {
    width: 250px;
    height: 50px;
    background-color: rgb(41, 41, 129);
    border-radius: 10px 0px 0px 10px;
    padding: 6px;
    display: flex;
}

.vendi-input {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    border: none;
    outline: none;
    padding: 5px;
    text-align: center;
    font-weight: 500;
    color: rgb(41, 41, 129);
    font-size: 18px;
}

.vendi-label {
    color: rgb(41, 41, 129);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.vendi-link {
    color: rgb(41, 41, 129);
    text-decoration: none;
}

.vendi-link-container {
    width: 250px;
    height: 50px;
    background-color: rgb(41, 41, 129);
    border-radius: 10px 0px 0px 10px;
    padding: 6px;
    display: flex;
    margin-top: 20px;
}

.vendi-link-container > div {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    border-radius: 10px;
    color: red;
}