.mangi-cucini {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url("../src/images/logo.svg");
    flex-direction: column;
}

.mangi-cucini-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 100px;
}

.mangi-cucini-links-container {
    height: 30%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin-top: 80px;
}

.mangi-cucini-mangi {
    width: 100%;
    height: 100%;
    background-color: rgb(41, 41, 129);
    border-radius: 10px 0px 0px 10px;
    padding: 6px;
    display: flex;
}

.mangi-cucini-cucini {
    width: 100%;
    height: 100%;
    background-color: rgb(41, 41, 129);
    border-radius: 10px 0px 0px 10px;
    padding: 6px;
    display: flex;
}

.mangi-cucini-mangi > div {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    border-radius: 10px;
    font-weight: 500;
    color: rgb(41, 41, 129);
}

.mangi-cucini-cucini > div {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    border-radius: 10px;
    font-weight: 500;
    color: rgb(41, 41, 129);
}

.mangi-cucini-link {
    width: 250px;
    height: 50px;
    color: rgb(41, 41, 129);
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
}
