.dove-sei {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-image: url("../src/images/logo.svg");
}

.dove-sei-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 100px;
}

.dove-sei-input-container {
    width: 300px;
    height: 50px;
    background-color: rgb(41, 41, 129);
    border-radius: 10px;
    padding: 6px;
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
}

.dove-sei-input {
    width: 80%;
    height: 100%;
    border-radius: 10px;
    border: none;
    outline: none;
    padding: 5px;
    text-align: center;
    font-weight: 500;
    color: rgb(41, 41, 129);
    font-size: 18px;
}

.dove-sei-input::placeholder {
    color: rgb(41, 41, 129);
}

.dove-sei-submit-button {
    width: 15%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    background-color: white;
    border-radius: 10px;
    color: rgb(41, 41, 129);
}

.dove-sei-localizzazione-container {
    width: 230px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px;
}

.dove-sei-geo-icon {
    color: rgb(41, 41, 129);
    font-size: 20px;
}

.dove-sei-localizzazione-container > h2 {
    color: rgb(41, 41, 129);
}