.cucini {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(255,220,94,255);
    flex-direction: column;
}

.cucini-title {
    color: rgb(41, 41, 129);
}

.cucini-inputs-container {
    width: 100%;
    margin-top: 50px;
    height: 60%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.cucini-input-container {
    width: 250px;
    height: 50px;
    background-color: rgb(41, 41, 129);
    border-radius: 10px 0px 0px 10px;
    padding: 6px;
    display: flex;
}

.cucini-input {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    border: none;
    outline: none;
    padding: 5px;
    text-align: center;
    font-weight: 500;
    color: rgb(41, 41, 129);
    font-size: 18px;
}

.cucini-label {
    font-weight: 500;
    color: rgb(41, 41, 129);
}

.cucini-orari-label {
    color: rgb(41, 41, 129);
    width: 300px;
    height: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    font-weight: 500;
    color: rgb(41, 41, 129);
}

.cucini-orari-container {
    width: 300px;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.cucini-orario-container {
    width: 120px;
    height: 30px;
    background-color: rgb(41, 41, 129);
    border-radius: 5px 0px 0px 5px;
    padding: 6px;
    margin: 10px;
}

.cucini-orario {
    width: 100%;
    height: 100%;
    background-color: white;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    color: rgb(41, 41, 129);
}

.cucini-link {
    height: 50px;
    color: rgb(41, 41, 129);
    text-decoration: none;
}

.cucini-link-container {
    width: 250px;
    height: 50px;
    background-color: rgb(41, 41, 129);
    border-radius: 10px 0px 0px 10px;
    padding: 6px;
    display: flex;
}

.cucini-link-container > div {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    border-radius: 10px;
    color: red;
}