.cassa {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: rgba(255,220,94,255);
}

.cassa-title {
    color: rgb(41, 41, 129);
    margin-bottom: 50px;
}

.cassa-links-container {
    height: 30%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.cassa-link-container {
    width: 100%;
    height: 100%;
    background-color: rgb(41, 41, 129);
    border-radius: 10px 0px 0px 10px;
    padding: 6px;
    display: flex;
}

.cassa-link-container > div {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    border-radius: 10px;
    font-weight: 500;
    color: rgb(41, 41, 129);
}

.cassa-link {
    width: 250px;
    height: 50px;
    color: rgb(41, 41, 129);
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
}
