.mangi {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    background-color: rgba(255,220,94,255);
    flex-direction: column;
}

.mangi-title {
    color: rgb(41, 41, 129);
    margin-top: 20px;
    text-align: center;
}

.mangi-other-dishes-container {
    width: 100%;
    height: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.mangi-other-dish {
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-decoration: none;
}

.mangi-other-dish-left > h4 {
    color: red;
}

.mangi-other-dish-left > h1 {
    color: rgb(41, 41, 129);
}

.mangi-other-dish-right > h4 {
    color: red;
}

.mangi-other-dish-right > h2 {
    color: rgb(41, 41, 129);
}