.scelta {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: rgba(255,220,94,255);
    flex-direction: column;
}

.scelta-input-container {
    width: 250px;
    height: 50px;
    background-color: rgb(41, 41, 129);
    border-radius: 10px 0px 0px 10px;
    padding: 6px;
    display: flex;
}

.scelta-input {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    border: none;
    outline: none;
    padding: 5px;
    text-align: center;
    font-weight: 500;
    color: rgb(41, 41, 129);
    font-size: 18px;
}

.scelta-input::placeholder {
    color: rgb(41, 41, 129);
}

.scelta-info {
    height: 150px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    color: rgb(41, 41, 129);
}