.login {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(255,220,94,255);
}

.login-form {
    width: 100%;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.login-input-container {
    width: 250px;
    height: 50px;
    background-color: rgb(41, 41, 129);
    border-radius: 10px 0px 0px 10px;
    padding: 6px;
    display: flex;
}

.login-input {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    border: none;
    outline: none;
    padding: 5px;
    text-align: center;
    font-weight: 500;
    color: rgb(41, 41, 129);
}

.login-label {
    font-weight: 500;
    color: rgb(41, 41, 129);
}

.login-submit-button {
    width: 100px;
    height: 30px;
    outline: none;
    border: none;
    border-radius: 5px;
    background-color: rgb(16, 94, 140);
    color: white;
}